import React from 'react';
import { motion } from 'framer-motion';
import { cn } from '../../utils';

/**
 * OptionButton Component
 * Reusable button for selecting options (e.g., question count, time limit)
 */
export const OptionButton = ({
  children,
  label,
  description,
  icon,
  onClick,
  selected = false,
  disabled = false,
  size = 'md',
  className = '',
  ...props
}) => {
  const sizeClasses = {
    sm: 'p-2 text-xs',
    md: 'p-4 text-sm',
    lg: 'p-6 text-base',
  };

  // Nếu có children, hiển thị children (backward compatible)
  if (children) {
    return (
      <motion.button
        onClick={onClick}
        disabled={disabled}
        className={cn(
          'rounded-xl border-2 transition-all duration-300 shadow-sm',
          sizeClasses[size],
          selected
            ? 'border-primary-600 bg-gradient-to-br from-primary-50 to-primary-100 font-semibold text-primary-700 shadow-md shadow-primary-200/50'
            : 'border-gray-300 bg-white hover:border-primary-400 hover:bg-primary-50/50 hover:shadow-md',
          disabled && 'opacity-50 cursor-not-allowed',
          className
        )}
        whileHover={!disabled ? { scale: 1.05 } : {}}
        whileTap={!disabled ? { scale: 0.95 } : {}}
        {...props}
      >
        {children}
      </motion.button>
    );
  }

  // Nếu có label/icon/description, hiển thị structured content
  return (
    <motion.button
      onClick={onClick}
      disabled={disabled}
      className={cn(
        'rounded-xl border-2 transition-all duration-300 shadow-sm text-left',
        sizeClasses[size],
        selected
          ? 'border-primary-600 bg-gradient-to-br from-primary-50 to-primary-100 font-semibold text-primary-700 shadow-md shadow-primary-200/50'
          : 'border-gray-300 bg-white hover:border-primary-400 hover:bg-primary-50/50 hover:shadow-md',
        disabled && 'opacity-50 cursor-not-allowed',
        className
      )}
      whileHover={!disabled ? { scale: 1.02 } : {}}
      whileTap={!disabled ? { scale: 0.98 } : {}}
      {...props}
    >
      <div className="flex flex-col items-center text-center space-y-1">
        {icon && (
          <span className="text-2xl mb-1" role="img" aria-label={label || ''}>
            {icon}
          </span>
        )}
        {label && (
          <span className="font-semibold block">{label}</span>
        )}
        {description && (
          <span className="text-xs text-gray-600 opacity-80">{description}</span>
        )}
      </div>
    </motion.button>
  );
};

